<template>
  <div id="new-employee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit.prevent="newEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="f_emp_id" required>
            <label>Employee_Id</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="f_name">
            <label>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="f_dept">
            <label>Dept</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="f_position">
            <label>Position</label>
          </div>
        </div>
      <button type="submit" class="btn">Submit</button>
      <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'

export default {
  name: 'new-employee',
  data () {
    return {
      f_emp_id: null,
      f_name: null,
      f_dept: null,
      f_position: null
    }
  },
  methods: {
    newEmployee () {
      console.log('New Employee')
      
      db.collection('employees').add({
        employee_id: this.f_emp_id,
        name: this.f_name,
        dept: this.f_dept,
        position: this.f_position
      })
        .then(emp => {
          this.$router.push('/')
        })
        .catch(error => console.log(err))
    }
  }
}
</script>


