<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Employees</h4></li>
      <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
        <div class="chip">{{employee.dept}}</div>:{{employee.name}} {{employee.employee_id}}
        <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-larg red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'

export default {
  name: 'dashboard',
  data () {
    return {
      employees: []
    }
  },
  created() {
    db.collection('employees').orderBy('employee_id').get().then(query => {
      query.forEach(emp => {
        const data_emp = {
          'id' : emp.id,
          'employee_id' : emp.data().employee_id,
          'name' : emp.data().name,
          'dept' : emp.data().dept,
          'position' : emp.data().position
        }
        this.employees.push(data_emp)
      })

      
    })
    
  }
}
</script>


