<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{ name }}</h4>
      </li>
      <li class="collection-item">Employee ID: {{ employee_id }}</li>
      <li class="collection-item">Position: {{ position }}</li>
      <li class="collection-item">Dept: {{ dept }}</li>
      <li class="collection-item">Position: {{ position }}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteEmployee" class="btn red">Delete</button>
  
    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-larg red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>

  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "view-employee",
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then((query) => {
        console.log("before Route");
        query.forEach((emp) => {
          next((vm) => {
            vm.employee_id = emp.data().employee_id;
            vm.name = emp.data().name;
            vm.dept = emp.data().dept;
            vm.position = emp.data().position;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then((query) => {
          console.log("fetch data");
          query.forEach((emp) => {
            this.employee_id = emp.data().employee_id;
            this.name = emp.data().name;
            this.dept = emp.data().dept;
            this.position = emp.data().position;
          });
        });
    },
    deleteEmployee() {
      if (confirm("Are you sure?")) {
        db.collection("employees")
          .where("employee_id", "==", this.$route.params.employee_id)
          .get()
          .then((query) => {
            query.forEach((emp) => {
              emp.ref.delete()
              this.$router.push('/')
            })
          })
      }
    },
  },
};
</script>


