<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
    <div class="row">
      <form @submit.prevent="updateEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="e_emp_id" required>
            <!-- <label>Employee_Id</label> -->
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="e_name">
            <!-- <label>Name</label> -->
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="e_dept">
            <!-- <label>Dept</label> -->
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="e_position">
            <!-- <label>Position</label> -->
          </div>
        </div>
      <button type="submit" class="btn">Submit</button>
      <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'

export default {
  name: 'edit-employee',
  data () {
    return {
      e_emp_id: null,
      e_name: null,
      e_dept: null,
      e_position: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then((query) => {
        console.log(to.params.employee_id);
        query.forEach((emp) => {
          next((vm) => {
            vm.e_emp_id = emp.data().employee_id;
            vm.e_name = emp.data().name;
            vm.e_dept = emp.data().dept;
            vm.e_position = emp.data().position;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then((query) => {
          console.log("fetch data");
          query.forEach((emp) => {
            this.e_emp_id = emp.data().employee_id;
            this.e_name = emp.data().name;
            this.e_dept = emp.data().dept;
            this.e_position = emp.data().position;
          })
        })
        .catch(error => console.log(err))
      
    },
    updateEmployee() {
      db.collection('employees').where('employee_id', '==', this.e_emp_id).get()
        .then(query => {
          query.forEach(emp =>{
            emp.ref.update({
              name: this.e_name,
              dept: this.e_dept,
              position: this.e_position
            })
            .then(() => {
              this.$router.push({name: 'view-employee', params: {employee_id: this.e_emp_id}})
            })
          })
        })
    }
  }
}
</script>


